  width: 100%;
  height: 100px;
  background: linear-gradient(45deg, #1f3c55, #34495e, #2c3e50);
  border-bottom: 3px solid #e74c3c;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  padding: 0 20px;
  position: relative;
}

#toolbar-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.toolbar-left {
  display: flex;
  align-items: center;
  gap: 14px;
}

.toolbar-center {
  display: flex;
  align-items: center;
  gap: 14px;
}

.toolbar-right {
  display: flex;
  align-items: center;
  gap: 18px;
  margin-left: auto;
}

#arraySize,
#sort,
#rewind {
  font-size: 16px;
  font-family: 'Courier Prime', monospace;
  font-weight: 500;
  color: #ecf0f1;
  display: inline-block;
  margin-left: 25px;
  margin-right: 15px;
  transition: all 0.3s ease;
}

#sort:hover,
#rewind:hover {
  color: #f39c12;
  transform: scale(1.05);
}

/* Refresh button Styling */
#generateArray,
#generateArrayX {
  font-size: 16px;
  font-family: 'Courier Prime', monospace;
  font-weight: 500;
  color: #ecf0f1;
  display: inline-block;
  /* margin-left: 25px; */
  margin-right: 3%;
  transition: all 0.3s ease;
}

.refresh-icon svg {
  transition: transform 0.4s ease-in-out;
  transform: scale(2);
}

.refresh-icon:hover svg {
  transform: rotate(360deg);
  color: #e74c3c;
}

/* Slider Styling */
.slider-outer {
  display: block;
}

.slider-outer span {
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  top: 2%;
}

.slider-container {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  gap: 10px;
  width: 50%;
}

#changeSize {
  outline: none;
  width: 85%;
  margin-right: 15px;
  border: 2px solid #f39c12;
  background-color: transparent;
  color: #f39c12;
  padding: 5px 15px;
  border-radius: 5px;
  transition: all 0.3s ease;
}

#changeSize:hover {
  background-color: #f39c12;
  color: #34495e;
}

.slider-button {
  background-color: #2c3e50;
  font-family: 'Courier Prime', monospace;
  font-size: 20px;
  padding: 5px 10px;
  margin-right: 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.negative {
  border: 2px solid rgba(0, 0, 255, 0.9);
  color: rgba(0, 0, 255, 0.9);
}

.positive {
  border: 2px solid rgba(255, 255, 0, 0.7);
  color: rgba(255, 255, 0, 0.7);
}

.slider-button:hover {
  background-color: #f39c12;
  color: #2c3e50;
  transform: scale(1.1);
}

.slider-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.separator {
  width: 5px;
  height: 55px;
  background-color: rgba(0, 0, 0, 0.6);
  margin-left: 15px;
  margin-right: 15px;
  display: inline-block;
  border-radius: 50%;
}

#sort,
#rewind {
  font-size: 16px;
  font-family: 'Playfair Display', serif;
  display: inline-block;
  margin-left: 15px;
  padding: 5px 10px;
  border-radius: 5px;
  background-color: transparent;
  color: #ecf0f1;
  border: 1px solid transparent;
  transition: all 0.3s ease;
}

#sort:hover,
#rewind:hover {
  color: #f39c12;
  border: 1px solid #f39c12;
  background-color: rgba(241, 94, 255, 0.1);
}

/* Input draggable button styling */
input[type=range] {
  -webkit-appearance: none;
  appearance: none;
  width: 120px;
  height: 8px;
  background: #2c3e50;
  border-radius: 5px;
  outline: none;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.3);
}

input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 20px;
  background: transparent;
  border-radius: 5px;
}

input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background: #e74c3c;
  border: 1.5px solid #fff;
  transition: transform 0.2s ease, background-color 0.3s ease;
}

input[type=range]:hover::-webkit-slider-thumb {
  /* background: #f39c12; */
  background: transparent;
  transform: scale(1.8);
}

input[type=range]:focus::-webkit-slider-thumb {
  /* background: #e74c3c; */
  background: transparent;
  border: 2px solid #f39c12;
  transform: scale(1.8);
}

