import React from "react";
import { connect } from "react-redux";
import "./ProgressBar.css";

function ProgressBar({ array, currentSorted, isRunning }) {
  if (!isRunning || !array.length) return null;
  const pct = Math.min(
    100,
    Math.round((currentSorted.length / array.length) * 100)
  );
  return (
    <div
      className="progress-shell"
      aria-label="Sorting progress"
      role="progressbar"
      aria-valuenow={pct}
      aria-valuemin={0}
      aria-valuemax={100}
    >
      <span className="progress-label">{pct}%</span>
      <div className="progress-track">
        <div className="progress-fill" style={{ width: pct + "%" }} />
        <div className="progress-glow" style={{ width: pct + "%" }} />
      </div>
    </div>
  );
}

const mapState = (s) => ({
  array: s.array,
  currentSorted: s.currentSorted || [],
  isRunning: s.isRunning,
});

export default connect(mapState)(ProgressBar);
