.mainContainer {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: calc(100vh - 100px); /* adjust if Toolbar is 100px tall */
}

.arrayContainer {
  flex: 1;
  transition: margin 0.3s ease;
}

