___CSS_LOADER_EXPORT___.push([module.id, `.status-bar {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem 1.25rem;
  align-items: center;
  padding: 0.6rem 1rem;
  background: linear-gradient(135deg,var(--surface-glow,#12151c),var(--surface-alt,#1c222b));
  border: 1px solid rgba(255,255,255,0.08);
  border-radius: 12px;
  box-shadow: 0 4px 14px -3px rgba(0,0,0,0.5), inset 0 0 0 1px rgba(255,255,255,0.04);
  -webkit-backdrop-filter: blur(12px) saturate(1.5);
  backdrop-filter: blur(12px) saturate(1.5);
  font-size: 0.85rem;
  margin: 0.75rem 0 1rem;
  color: var(--text-primary,#f5f7fa);
}

.status-section { display:flex; gap:0.3rem; align-items:center; }
.status-section .label { opacity:0.6; font-weight:500; letter-spacing:0.5px; text-transform:uppercase; font-size:0.65rem; }
.status-section .value { font-weight:600; font-variant-numeric:tabular-nums; }

@media (max-width: 640px) {
  .status-bar { font-size:0.75rem; padding:0.5rem 0.75rem; }
  .status-section .label { display:none; }
}
