___CSS_LOADER_EXPORT___.push([module.id, `.progress-shell { position:relative; width:380px; max-width:42vw; height:38px; background:linear-gradient(135deg,#1d2732,#16202b); border:1px solid rgba(255,255,255,0.08); border-radius:14px; box-shadow:0 4px 18px -4px rgba(0,0,0,0.65), inset 0 0 0 1px rgba(255,255,255,0.06); overflow:hidden; display:flex; align-items:center; padding:0 14px 0 70px; font-family:'Inter', system-ui; }
.progress-label { position:absolute; left:18px; top:50%; transform:translateY(-50%); font-weight:700; font-size:15px; letter-spacing:1px; color:#fff; text-shadow:0 2px 4px rgba(0,0,0,0.55); mix-blend-mode:screen; }
.progress-track { position:relative; flex:1; height:14px; background:linear-gradient(90deg,#263549,#1d2734); border-radius:8px; overflow:hidden; }
.progress-fill { position:absolute; inset:0; background:linear-gradient(90deg,#ff7e36,#ff5f56,#ff2f7d); box-shadow:0 0 8px 2px rgba(255,95,86,0.5); transition:width .35s cubic-bezier(.4,.8,.3,1); }
.progress-glow { position:absolute; inset:0; background:radial-gradient(circle at 10% 50%,rgba(255,255,255,0.35),transparent 70%); mix-blend-mode:overlay; pointer-events:none; transition:width .35s cubic-bezier(.4,.8,.3,1); }
@media (max-width:820px){ .progress-shell { width:300px; height:34px; padding-left:62px; } .progress-label { font-size:13px; left:14px; } }
